___CSS_LOADER_EXPORT___.push([module.id, `* {
  padding: 0;
  margin: 0;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.skip-link {
  position: absolute;
  font-weight: bold;
  text-decoration: none;
  top: -44px;
  left: 0;
  padding: 8px;
  background-color: #ce5a67;
  color: #fcf5ed;
  z-index: 100;
}

.skip-link:focus {
  top: 0;
}

.text-primary {
  color: #71717a;
  font-size: 16px;
}

#hamburgerButton {
  padding: 12px 16px;
}

/* AppBar */

.app-bar {
  padding: 8px 16px;
  background-color: white;
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 10px;
  position: sticky;
  top: 0;
  z-index: 99;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}

.app-bar .app-bar-menu {
  display: flex;
  align-items: center;
}

.app-bar .app-bar-menu button {
  background-color: transparent;
  border: none;
  font-size: 18px;
  padding: 8px;
  cursor: pointer;
}

.app-bar .app-bar-brand {
  display: flex;
  align-items: center;
}

.app-bar .app-bar-brand h1 {
  color: #3f2305;
  text-transform: uppercase;
  font-size: 22px;
  user-select: none;
}

.app-bar .app-bar-navigation {
  position: absolute;
  top: 50px;
  left: -180px;
  width: 150px;
  transition: all 0.3s;
  padding: 8px 0 8px 0;
  background-color: white;
  overflow: hidden;
}

.app-bar .app-bar-navigation.open {
  left: 0;
}

.app-bar .app-bar-navigation ul li a {
  display: inline-block;
  text-decoration: none;
  color: #3f2305;
  font-weight: 500;
  padding: 12px 20px 10px;
  margin-bottom: 5px;
  width: 100%;
  box-sizing: border-box;
  transition: all 0.2s ease-in-out;
}

.app-bar-navigation ul li a:hover {
  background-color: #f1ece4;
}

/*
    Main Content
  */

main {
  padding: 32px;
  flex: 1;
}

.content {
  margin: 0 auto;
  min-height: 100%;
}

.content .content-heading {
  font-weight: normal;
}

/* Restos */

.restos {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 30px;
  margin: 32px 0;
}

/*
    resto Item
  */

.resto-item {
  width: 100%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  overflow: hidden;
}

.resto-item-header {
  position: relative;
}

.resto-item .resto-item-header .resto-item-header-poster {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.resto-item .resto-item-header .resto-item-header-rating {
  position: absolute;
  padding: 8px 12px 8px;
  bottom: 20px;
  left: 0;
  display: inline-block;
  background-color: #3f2305;
  color: white;
}

.resto-item .resto-item-header-rating-score {
  margin-left: 10px;
}

.resto-item .resto-item-content {
  padding: 20px 20px 25px;
  box-sizing: border-box;
}

.resto-item .resto-item-content h3 {
  padding-bottom: 12px;
}

.resto-item .resto-item-content h3 a {
  color: #3f2305;
  text-decoration: none;
  padding: 12px 0 12px;
}

.resto-item .resto-item-content p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4; /* number of lines to show */
  -webkit-box-orient: vertical;
}

/* Resto */

.resto {
  margin: 0 auto;
  width: 100%;
  max-width: 800px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 18px 16px;
}

.resto .resto-poster {
  width: 100%;
  max-width: 400px;
}

.information {
  margin-top: 25px;
}

.resto .resto-info h4 {
  margin: 12px 0 8px;
}

.resto-food h4,
.resto-drink h4 {
  margin-bottom: 10px;
}

.resto-drink {
  margin-top: 24px;
}

.resto-description h3 {
  margin: 10px 0 15px;
}

.customer-reviews h3 {
  margin: 20px 0 20px;
}

.customer-reviews ul {
  list-style-type: none;
}

.customer-reviews ul li {
  background-color: #f1ece4;
  text-decoration: none;
  margin-bottom: 20px;
  padding: 20px;
  border-radius: 6px;
  line-height: 28px;
}

.customer-reviews ul li .date {
  font-style: italic;
  font-size: 12px;
}

#review-form {
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

#review-form h3 {
  margin: 20px 0 20px;
}

#review-name {
  padding: 16px 12px 16px;
  margin-bottom: 14px;
}

#review-content {
  padding: 16px 12px 16px;
  margin-bottom: 8px;
}

#review-form button {
  margin: 15px 0 30px;
  padding: 16px;
  width: 115px;
  background-color: #3f2305;
  color: white;
  border-radius: 4px;
}

/* LIke */

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #dc8686;

  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;

  font-size: 18px;
  color: white;
  cursor: pointer;
}

/* Footer */

footer {
  padding: 30px;
  background-color: #3f2305;
}

footer p {
  text-align: center;
  color: #aaaaaa;
  font-size: 12px;
  line-height: 25px;
}

footer p a {
  color: white;
  text-decoration: none;
  padding: 16px 0 16px;
}
